@use '/src/Assets/sass/variables';
.webProjects{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    margin: 0px 15%;
    margin-bottom: 50px;
    padding-bottom: 10px;
    padding-top: 10px;
    min-height: 500px;
    gap: 20px;
    @media screen and (max-width: variables.$screenWidth_mobile){
        margin: 0;
    }
}